<template>
    <main class="main subscribe__wrapper">
        <Preloader :is-visible="showLoaderStatus" />
        <MainTitle :text="'Подписка'"
                   :with-logo="true"
        />
        <ServiceSelect />
        <PayMethod />
        <NewStories />
        <DurationSelect />
    </main>
</template>

<script>
    import { defineAsyncComponent, defineComponent } from "vue";
    import { API } from "../components/Constants";

    export default defineComponent({
        name: "App",
        components: {
            Preloader: defineAsyncComponent(() => import("@/components/Preloader.vue")),
            MainTitle: defineAsyncComponent(() => import("@/components/MainTitle.vue")),
            ServiceSelect: defineAsyncComponent(() => import("@/components/ServiceSelect.vue")),
            NewStories: defineAsyncComponent(() => import("@/components/NewStories.vue")),
            DurationSelect: defineAsyncComponent(() => import("@/components/DurationSelect.vue")),
            PayMethod: defineAsyncComponent(() => import("@/components/PayMethod.vue")),
        },
        data() {
            return {
                showLoader: true,
                servicesLoaded: false,
                storiesLoaded: false,
            };
        },
        computed: {
            showLoaderStatus() {
                return this.$store.state.showLoaderStatus;
            },
            userID() {
                return this.$store.state.userInfo.USER_ID;
            },
            abonentType() {
                return this.$store.state.abonentInfo.abonentType;
            },
            isAuthorityAbonentType() {
                return this.$store.getters.isAuthorityAbonentType;
            }
        },
        watch: {
            userID() {
                this.$store.dispatch("sendUserEvent", "YardSubView");
            },
            abonentType() {
                this.checkAbonentType();
            },
        },
        mounted() {
            this.$nextTick(() => {
                document.onreadystatechange = () => {
                    if (document.readyState === "complete") {
                        this.showLoader = false;
                    }
                };
            });
            this.checkAbonentType();
        },
        // TODO: убрать после фикса на ios перехода на корректный экран.
        // Проблема: в МП приложение на ios пользователей с типом подписки "free"
        // перебрасывает на данный экран; Но абонентов с типом "vip" и "free"
        // должно редиректить на страницу /subscribe_vip/
        methods: {
            checkAbonentType() {
                if (this.isAuthorityAbonentType) {
                    this.$router.push(`/subscribe_vip/?token=${API.TOKEN}`);
                }
            },
        },
    });
</script>

<style>
@import "../assets/main.css";

.subscribe__wrapper {
  min-height: calc(100vh + 40px);
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  font-size: 16px;
  font-family: "SF Pro", serif;
  color: #061029;
  overflow: auto;
  margin-top: 40px;
  padding-bottom: 194px;
}

.container {
  width: calc(100% - 32px);
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
